// ];


const ChooseColis = (props) => {
  const [tableshow, settableshow] = useState([])
  const [selected, setSelected] = useState([]);
  const [scales, setscales] = useState([])
  const columns = [
    {
      dataField: "colis.index_colis",
      text: "Num colis",
      headerStyle: {
        backgroundColor: '#adaeb0',
        fontWeight: 'bold',
        fontSize: '18px',
      },
      clickToSelect: false,
      sort:true

    },
    {
      dataField: "colis.lot",
      text: "Num colis",
      headerStyle: {
        backgroundColor: '#adaeb0',
        fontWeight: 'bold',
        fontSize: '18px',
      }

    },
    {
      dataField: "colis.name_complete",
      text: "client",
      headerStyle: {
        backgroundColor: '#adaeb0',
        fontWeight: 'bold',
        fontSize: '18px',
      }

    },
    {
      dataField: "colis.gouvernorat",
      text: "gouvernorat",
      headerStyle: {
        backgroundColor: '#adaeb0',
        fontWeight: 'bold',
        fontSize: '18px',
      }

    },
    {
      dataField: "colis.gouvernorat",
      text: "Status",

      headerStyle: {
        backgroundColor: '#adaeb0',
        fontWeight: 'bold',
        fontSize: '18px',
      },
      formatter: (cell, row) => {
        return (row.delivery_status === 1 ? "Livré" : "retour" )
    },

    },

    {
      dataField: "colis.price",
      text: "Prix",
      headerStyle: {
        backgroundColor: '#adaeb0',
        fontWeight: 'bold',
        fontSize: '18px',
      },

      style: (cell, row, rowIndex, colIndex) => {

        return {
          backgroundColor: '#c8e6c9', fontWeight: 'bold',
          fontSize: '18px'
        }

      }

    },
    {
      dataField: "prixlivara",
      text: "Prix Livraison",
      headerStyle: {
        backgroundColor: '#adaeb0',
        fontWeight: 'bold',
        fontSize: '18px',
      },

      style: (cell, row, rowIndex, colIndex) => {
        return row.prixlivara == 0 ?
          {
            textAlign: "center",
            backgroundColor: '#ff00ff', fontWeight: 'bold',
            fontSize: '18px'
          } : {
            textAlign: "center",
            backgroundColor: '#c8e6c9', fontWeight: 'bold',
            fontSize: '18px'
          }

      }




    },
    {
      dataField: "id",
      headerStyle: {
        backgroundColor: '#adaeb0',
        fontWeight: 'bold',
        fontSize: '18px',
      },
      text: "supprimer",
      formatter: (cell, row, rowIndex, formatExtraData) => {


        return (
            <Button
                variant="danger"
                onClick={() => deleteinvoce(row.id)}
            >
                {" "}
                <span className="fa fa-trash"></span>
            </Button>
        );
      },
      style: { textAlign: "center" },

  },


  ];
  const forcagedone = (cell, row, rowIndex, formatExtraData) => {


    return (
        <Button
            variant="warning"
            onClick={() => deleteinvoce(row.colis.id)}
        >
            {" "}
            <span className="fa fa-forward"></span>
        </Button>
    );
}
const deleteinvoce = (id) => {
  const config = {
      headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Headers': 'Authorization',
          "x-access-token": localStorage.getItem('token'),
          'Authorization': 'Bearer ' + localStorage.getItem('token'),
      },
  };
  axios.delete(urlbackend + "/facture/"+id, config).then((response) => {
      alert("ok")
      getdata()


  }).catch(() => {

      console.log("no")
  })

}
  const getdata = () => {
    console.log(props.providerget)
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Headers': 'Authorization',
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
      },
    };
    axios.get(urlbackend + "/facture/" + props.providerget, config)
      .then(response => {
        console.log("this respon list", response.data)

        var table1 = response.data.data.rows1.map(e => (response.data.data.scale[e.colis.gouvernorat] ? { ...e, prixlivara: (response.data.data.scale[e.colis.gouvernorat] * e.colis.lot) } : { ...e, prixlivara: 0 }))

        var table2 = response.data.data.rows2.map(e => (e.delivery_status === 0 ? { ...e, prixlivara: (response.data.data.scale["RETOUR"] * e.colis.lot) } : { ...e, prixlivara: (response.data.data.scale["RETOUR"] * e.colis.lot) }))
       // settableshow(response.data.data.rows1.map(e => (response.data.data.scale[e.colis.gouvernorat] ? { ...e, prixlivara: (response.data.data.scale[e.colis.gouvernorat] * e.colis.lot) } : { ...e, prixlivara: 0 })))
      //  settableshow(response.data.data.rows2.map(e => (e.delivery_status === 0 ? { ...e, prixlivara: (response.data.data.scale["RETOUR"] * e.colis.lot) } : { ...e, prixlivara: (response.data.data.scale["RETOUR"] * e.colis.lot) })))
      settableshow(table1.concat(table2)) 
      setscales(response.data.data.scale)
        console.log(scales)

        console.log('scale', response.data.data.scale)

      })
      .catch(e => {
        console.log(e)
      })
  }
  useEffect(() => {
    getdata()

  }, [])
  const handleOnSelect = (row, isSelect) => {

    if (isSelect) {
      setSelected((prevSelected) => [...prevSelected, { id_facture: row.id, frais_livraision: row.prixlivara, price: row.colis.price,statuslivraison:row.delivery_status }]);
    } else {
      setSelected((prevSelected) => prevSelected.filter((x) => x.id_facture !== row.id));
    }
  };

  // const handleOnSelectAll = (isSelect, rows) => {
  //   const ids = rows.map((r) => r.id);
  //   if (isSelect) {
  //     setSelected(ids);
  //   } else {
  //     setSelected([]);
  //   }
  //   return [1];
  // };
  const clacultotal = () => {
    var myTotal = 0;  // Variable to hold your total

    for (var i = 0, len = selected.length; i < len; i++) {

      myTotal += parseFloat(selected[i].frais_livraision);  // Iterate over your first array and then grab the second element add the values up
    }
    return myTotal
    // return selected.reduce((a, b)=>a.test+b.test)

  }
  const clacultotalfrais = () => {
    var myTotal = 0;  // Variable to hold your total

    for (var i = 0, len = selected.length; i < len; i++) {
      if(selected[i].statuslivraison===1){
        myTotal += parseFloat(selected[i].price); 
      }
      // Iterate over your first array and then grab the second element add the values up
    }
    return myTotal
    // return selected.reduce((a, b)=>a.test+b.test)

  }
  const prefacture = () => {

    // {
    //   "provider": "",
    //   "frais_total": null,
    //   "nb_colis": "",
    //   "facture": [
    //     {
    //       "id_facture": "",
    //       "frais_livraision": 0
    //     }
    //   ]
    // }
    console.log({

      "provider": props.providerget,
      "frais_total": clacultotal(),
      "total_livraision": clacultotalfrais(),

      "nb_colis": selected.length,
      "factures": selected
    })
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Headers': 'Authorization',
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
      },
    };
    axios.put(urlbackend + "/facture/to_paid", {

      "provider": props.providerget,
      "frais_total": clacultotal(),
      "total_livraision": clacultotalfrais(),

      "nb_colis": selected.length,
      "factures": selected
    }, config)
      .then(response => {
        console.log("this respon list", response.data)
        setSelected([])
        getdata()

      })
      .catch(e => {
        console.log(e)
      })

  }


  return (
    <div style={{ padding: "20px" }}>

      <Table>
        <tr>
          <th style={{
            fontWeight: 'bold',
            fontSize: '18px'
          }}>Frais livraison</th>
          <td>{selected.length && clacultotal()}</td>

        </tr>
        <tr>
          <th style={{
            fontWeight: 'bold',
            fontSize: '18px',
            width: "20%"
          }}>nombre de colis</th>
          <td>{selected.length}</td>
        </tr>
        <tr>
          <th style={{
            fontWeight: 'bold',
            fontSize: '18px'
          }}>valeur Colis</th>
          <td>{selected.length && clacultotalfrais()}</td>
        </tr>
        <tr>
          <th style={{
            fontWeight: 'bold',
            fontSize: '18px'
          }}> Remboursement</th>
          <td>{selected.length && (clacultotalfrais()-clacultotal())}</td>
        </tr>
      </Table>


      {selected.length !== 0 && <Button style={{ "float": "right" }} variant="secondary"
        onClick={() => prefacture()}> <span className="fa fa-tags "></span>pré facture</Button>}
      <BootstrapTable
        keyField="id"
        data={tableshow}
        columns={columns}
        headerClasses="header-class"
        cellEdit={cellEditFactory({ mode: 'click' })}
        selectRow={{
          mode: "checkbox",
          clickToSelect: false,
          clickToEdit: true,
          bgColor: '#d1e0f3',
          onSelect: handleOnSelect,
          hideSelectAll: true,
          selectionRenderer: ({ mode, ...rest }) => (
            <div>
              <input
                type={mode}
                checked={rest.checked}
                disabled={rest.disabled}
              // rowindex={rest.rowIndex}
              // onClick={() => console.log(rest.checked)}
              // onChange={()=>{}}
              />
            </div>
          ),
          selectionHeaderRenderer: ({ mode, ...rest }) => (
            <div>
              <input
                type={mode}
                checked={rest.checked}
              // disabled={rest.disabled}
              // rowindex={rest.rowIndex}
              // onClick={() => console.log(rest.checked)}
              // onChange={()=>{}}
              />
            </div>
          )
        }}
      />
    </div>
  );
};

export default ChooseColis;